













export default {
	data() {
		return {
			renickname:'',
		};
	},
	onLoad() {
	},
	methods: {
		changeNickname: function(renickname) {
			var _this = this;
			uni.request({
				url: this.apiServer + '/user/nickname?id='+uni.getStorageSync('login_key').userId,
				method: 'put',
				data:renickname,
				header: { 'content-type': 'application/json' },
				success: res => {
					uni.navigateBack();
				}
			});
		}
	}
};
